* {
  box-sizing: border-box;
}

html,
body {
  font-size: 16px;
  font-family: "Roboto", -apple-system, system-ui, BlinkMacSystemFont, Segoe UI,
    Open Sans, Helvetica Neue, Arial, sans-serif;
  margin: 0;
  padding: 0;
  height: 100%;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
  cursor: pointer;
}
#root {
  height: 100%;
}

.zel-button {
  border: 0;
  background: transparent;
  outline: 0;
  text-align: left;
  font-family: "Roboto";
  color: #27166f;
  font-size: 0.875rem;
  padding: 0.75rem;
  cursor: pointer;
}

.zel-button__full {
  display: block;
  width: 100%;
}

